import request from '@/utils/request';
import { AxiosResponse } from 'axios';
import { Tabs, TABS } from '@/enum';
import { GetTopicsParams, CommRes, GetTopicDetailParams, Topic, TopicDetail } from '@/interface';
import { DEV_ACCESSTOKEN } from '@/utils/config';
import { oneOf, length } from '@/validate';

export const defaultGetTopicsParams: GetTopicsParams = {
  limit: 20,
  page: 1,
  mdrender: true,
};
/**
 * get /topics 主题首页
 * 
 */
export async function getTopics(getTopicsParams: GetTopicsParams = {}): Promise<Array<Topic> | undefined> {
  try {
    // 合并参数
    const params: GetTopicsParams = {};
    Object.assign(params, defaultGetTopicsParams, getTopicsParams);
    if (typeof params.tab === 'number') {
      // 转化tab
      params.tab = Tabs[<Tabs>params.tab];
    }
    // 验证tab
    oneOf<string>(<string>params.tab, TABS);
    const { data } = await request.get('/topics', { params }) as AxiosResponse<CommRes<Array<Topic>>>;
    return data.data;
  } catch (error) {
    // TODO 报错
    console.error(error);
    return undefined;
  }
}
/**
 * get /topic/:id 主题详情
 */
export async function getTopicDetail(id: string, params: GetTopicDetailParams = {
  accesstoken: DEV_ACCESSTOKEN
}): Promise<TopicDetail | undefined> {
  try {
    id = id.trim();
    // 验证 20 <= length <= 30
    length(id, 20, 30);
    const { data } = await request.get(`/topic/${id}`, { params })  as AxiosResponse<CommRes<TopicDetail>>;
    return data.data;
  } catch (error) {
    // TODO 报错
    console.error(error);
    return undefined;
  }
}