export function oneOf<T>(value: T, arr: Array<T>) {
  if (!arr.includes(value)) {
    console.error(`validate fail value: ${value}, not oneOf arr: ${arr}`);
    throw new Error(`validate fail value: ${value}, not oneOf arr: ${arr}`);
  }
}
interface HasLength{
  length: number;
}
export function length(value: HasLength, min: number = 0, max?: number) {
  if (typeof max === 'undefined') {
    max = min;
  }
  if ((value.length < min) && (value.length > max)) {
    console.error(`validate fail length: ${value.length}, not max than: ${min}, or not min than: ${max}`);
    throw new Error(`validate fail length: ${value.length}, not max than: ${min}, or not min than: ${max}`);
  }
}
