/* eslint-disable */
import axios, { AxiosRequestConfig, AxiosResponse } from 'axios';
import { axiosConfig } from '@/utils/config';
import { CommRes } from '@/interface';
import store from '@/store';
import { SHOW_SNACKBAR } from '@/store/types';
import { RootShowSnackBarPayload } from '@/store/interface';

const pending: Array<{
  url: string,
  cancel: Function
}> = [];
const { CancelToken } = axios;
const request = axios.create(axiosConfig);
const removePending = (config: AxiosRequestConfig) => {
  for (const p in pending) {
    const list = pending[p];
    if (list.url === `${config.url}&request_type=${config.method}`) {
      list.cancel();
      pending.slice(p as any, 1);
    }
  }
};
request.interceptors.request.use((config) => {
  removePending(config);
  config.cancelToken = new CancelToken((c) => {
    pending.push({ url: `${config.url}&request_type=${config.method}`, cancel: c });
  });
  return config;
});

request.interceptors.response.use(
  (res: AxiosResponse<CommRes<any>>) => {
    removePending(res.config);
    // 后端返回success不为true报错
    if (res.data.success !== true) {
      console.error(res.data.error_msg || '网络错误');
      store.commit(SHOW_SNACKBAR, { message: res.data.error_msg || '网络错误' }  as RootShowSnackBarPayload)
      return Promise.reject(res.data.error_msg);
    }
    return res;
  },
  (err) => {
    if (err.message) {
      store.commit(SHOW_SNACKBAR, { message: err.message }  as RootShowSnackBarPayload);
    }
    console.error(err);
    return Promise.reject(err);
  },
);

export default request;
